import jwt_decode from 'jwt-decode'

export default {
  props: {
    verifyData: {
      required: true,
      type: Object,
    },
  },
  data() {
    return {
      tabListVrf: [
        { key: 'sms', scopedSlots: { tab: 'titleSlot' } },
        { key: '2fa', scopedSlots: { tab: 'titleSlot' } },
      ],
      activeVrfType: 'sms',
      vrf: {
        SmsCode: '',
        EmailCode: '',
        TwoFaCode: '',
      },
      vrfTypes: [],
      isSmsCodeSent: false,
      smsTimer: 60,
      smsIntrvl: null,
      isloadingVerifyData: false,

      showError: false,
      errorMsg: '',
    }
  },
  mounted() {
    if (this.verifyData.Token) {
      const { vrf } = jwt_decode(this.verifyData.Token)
      this.vrfTypes = vrf.split(',').length > 1 ? vrf.split(',') : [vrf]
    }
  },
  methods: {
    async verify() {
      const { $axios, $config } = this
      try {
        this.isloadingVerifyData = true
        const response = await $axios.post(
          $config.AUTH_API_URL + '/SecurityVerify',
          {
            Token: this.verifyData.Token,
            ...this.vrf,
          }
        )
        this.isloadingVerifyData = false

        if (response.data.Success) {
          this.clearTimers()
          this.$router.push(this.localeRoute('/account/security'))
        } else {
          this.showError = true
          this.errorMsg = response.data.Message
        }
      } catch (e) {
        if (e.response) {
          this.showError = true
          this.errorMsg = e.response.data.Message
        }
      }
    },
    cancelVerify() {
      this.clearTimers()
      this.verifyData.Token = null
      this.verifyData.showDialog = false
    },
    async sendCode(type) {
      const { $axios, $config } = this
      const payload = {
        Type: type,
        Token: this.verifyData.Token,
      }
      try {
        const response = await $axios.post(
          $config.AUTH_API_URL + '/SendOTP',
          payload
        )
        if (response.data.Success) {
          this.isSmsCodeSent = true
          this.startTimer('SMS')
        }
      } catch (e) {
        // this.$router.push('/account')
      }
    },
    startTimer(type) {
      if (type === 'SMS') {
        this.smsIntrvl = setInterval(() => {
          if (this.smsTimer === 0) {
            this.smsTimer = 60
            this.isSmsCodeSent = false
            clearInterval(this.smsIntrvl)
          }
          this.smsTimer -= 1
        }, 1000)
      }
    },
    clearTimers() {
      clearInterval(this.smsIntrvl)
      this.smsTimer = 60
      this.isSmsCodeSent = false
    },
  },
  watch: {
    activeVrfType(val) {
      if (val === 'sms') {
        this.vrf.TwoFaCode = ''
      }
      if (val === '2fa') {
        this.vrf.SmsCode = ''
      }
    },
  },
}
